  onShowAnswer() {
  
    this.xanswer = !this.xanswer

}
  onNextCard() {

    this.xanswer = false ;

    var nbr = Math.floor(Math.random() * Global.studyCards.length); 

    if (nbr ==  this.idx) {
       
        if (nbr == 0) {
          nbr = 1;
        }
        else {
           nbr--  ;
        }
    }
    
    this.idx = nbr ;

   
  
     this.studyCard = Global.studyCards[this.idx][1] ;


     


  }

